import React from 'react';
import {useSelector} from "react-redux";
import {RootState} from "../../store/reducers";
import styles from "./todaystatistics.module.css";
import {IDayliStatistics} from "../../store/types";
import {selectTodayStatistics} from "../../store/selectors/statistics";
import {getFocus, secondsToMinutes} from "../../store/utils/statistics-api";

export function TodayStatistics() {
    const {timeOfWork, timeOfPause, numberOfStops, numberPomidors} = useSelector<RootState, IDayliStatistics>(selectTodayStatistics);

    return (
        <div className={styles.wrapper}>
            <span className={styles.leftSpan}> ()</span>
            <span className={styles.rightSpan}>{secondsToMinutes(timeOfWork)}</span>
            <span className={styles.leftSpan}> ()</span>
            <span className={styles.rightSpan}>{secondsToMinutes(timeOfPause)}</span>
            <span className={styles.leftSpan}> </span>
            <span className={styles.rightSpan}>{numberOfStops}</span>
            <span className={styles.leftSpan}>()</span>
            <span className={styles.rightSpan}>{getFocus(timeOfWork, timeOfPause)}</span>
            <span className={styles.leftSpan}> </span>
            <span className={styles.rightSpan}>{numberPomidors}</span>
        </div>
  );
}
