import React from 'react';
import {GenericDraggableList, IItem} from "../../GenericDraggableList";
import {useDispatch, useSelector} from "react-redux";
import {RootState} from "../../store/reducers";
import {TaskListState} from "../../store/reducers/taskListReducer";
import styles from './tasklist.module.css'
import {
    decPomidor,
    deleteTask,
    editTitleTask,
    incPomidor,
    moveTask,
    selectTask
} from "../../store/action-creators/taskList";
import classNames from "classnames";
import {Task} from "./Task";

export function TaskList() {
    const tasksState = useSelector<RootState, TaskListState>(state => state.taskList);
    const dispatch = useDispatch();

    if (tasksState.tasks.length === 0){
        return (
            <h4>  </h4>
        );
    }

    const items:IItem[] = tasksState.tasks.map(task =>
        ({
            content:(
                <Task
                    title={task.title}
                    isSelected={task.id === tasksState.selectedId}
                    pomidors={task.pomidors}
                    onIncPomidorsClick={() => dispatch(incPomidor(task.id))}
                    onDecPomidorsClick={() => dispatch(decPomidor(task.id))}
                    onDeleteTaskClick={() => dispatch(deleteTask(task.id))}
                    onBlur={() => dispatch(selectTask(-1))}
                    onChangeTitle={value => {
                        if (value) {
                            dispatch(editTitleTask(task.id, value))
                        }
                    }}/>),
            id:task.id.toString(),
            className:classNames(styles.itemList, {[styles.selected]:task.id === tasksState.selectedId}),
            onClick:(id)=>dispatch(selectTask(Number(id)))
        }));

    return (
            <GenericDraggableList list={items} moveItem={(dragIndex, hoverIndex) => dispatch(moveTask(dragIndex, hoverIndex))}/>
    );
}
