import React from 'react';
import styles from './header.module.css'
import {Link} from "react-router-dom";

export function Header() {
    return (
        <header className={styles.header}>
            <Link to={`${process.env.PUBLIC_URL}/`} className={styles.link}>POMODORO_BOX</Link>
            <Link to={`${process.env.PUBLIC_URL}/statistics`} className={styles.link}></Link>
        </header>
  );
}
