import {RootState} from "../reducers";
import {IDayliStatistics} from "../types";
import {getEndOfWeek, getToday} from "../../utils/date-api";
import addDays from "date-fns/addDays";

export function getEmptyDailyStatistics():IDayliStatistics{
    return {
        timeOfWork:0,
        timeOfPause:0,
        numberOfStops:0,
        numberPomidors:0,
    }
}

export const selectStartDate = (state:RootState) => state.statistics.startDate;
export const selectEndDate = (state:RootState) => getEndOfWeek(state.statistics.startDate);

export function selectTodayStatistics(state:RootState):IDayliStatistics{
    const today = getToday();

    return state.statistics.history.get(today.getTime())! ?? getEmptyDailyStatistics();
}

export function selectDataSet(state:RootState):IDayliStatistics[]{
    const dataSet:IDayliStatistics[] = [];

    const startDate = state.statistics.startDate;
    const history = state.statistics.history;

    for(let i = 0;i < 7;i++)
    {
        const today = addDays(startDate, i);
        const dayliStatistics = history.get(today.getTime()) ?? getEmptyDailyStatistics();
        dataSet.push(dayliStatistics);
    }

    return dataSet;
}
