export const SET_START_DATE = 'SET_START_DATE';

export type SetStartDateAction = {
    type: typeof SET_START_DATE;
    date:Date;
}

export const WRITE_POMIDOR_COMPLETE_EVENT = 'WRITE_POMIDOR_COMPLETE_EVENT';

export type WritePomidorCompleteEventAction = {
    type: typeof WRITE_POMIDOR_COMPLETE_EVENT;
    workTime:number;
    pauseTime:number;
    numberOfStops:number;
}

export type StatisticsActions = SetStartDateAction | WritePomidorCompleteEventAction;