import React from 'react';
import {useDispatch, useSelector} from "react-redux";
import {RootState} from "../../store/reducers";
import {TaskListState} from "../../store/reducers/taskListReducer";
import styles from './completedtasklist.module.css'
import {GenericList, IItem} from "../../GenericList";
import {Block} from "../../Block";
import {CompletedTask} from "./CompletedTask";
import {deleteTask} from "../../store/action-creators/taskList";

export function CompletedTaskList() {
    const tasksState = useSelector<RootState, TaskListState>(state => state.taskList);
    const dispatch = useDispatch();

    if (tasksState.completedTasks.length === 0){
        return null
    }

    const items:IItem[] = tasksState.completedTasks.map(task =>
        ({
            content:<CompletedTask title={task.title} onDeleteTaskClick={() => dispatch(deleteTask(task.id))}/>,
            id:task.id.toString(),
            className:styles.itemList,
        }));

    return (
        <Block title=''>
            <GenericList list={items} />
        </Block>
    );
}
