import React from 'react';
import {useSelector} from "react-redux";
import {RootState} from "../../store/reducers";
import {IDayliStatistics} from "../../store/types";
import {BarElement, CategoryScale, Chart as ChartJS, Legend, LinearScale, Title, Tooltip, TooltipItem} from "chart.js";
import {Bar} from "react-chartjs-2";
import styles from "./barchart.module.css";
import {selectDataSet} from "../../store/selectors/statistics";
import {getFocus, secondsToHours} from "../../store/utils/statistics-api";

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

const labels = ['', '', '', '', '', '', ''];

export function BarChart() {
    const dataSet = useSelector<RootState, IDayliStatistics[]>(selectDataSet);

    const options = {
        responsive: true,
        maintainAspectRatio:false,
        plugins: {
            legend: {
                position: 'top' as const,
            },
            title: {
                display: true,
                text: '   ',
            },
            tooltip: {
                callbacks: {
                    afterLabel: function(context:TooltipItem<any>) {
                        const {timeOfWork, timeOfPause, numberPomidors, numberOfStops} = dataSet[context.dataIndex];
                        const workInfo = context.datasetIndex === 0?`  - ${secondsToHours(timeOfPause)}`:`  - ${secondsToHours(timeOfWork)}`;
                        return `${workInfo}\n - ${getFocus(timeOfWork, timeOfPause)}\n  - ${numberOfStops}\n  - ${numberPomidors}`;
                    }
                }
            }
        },
    };

    const data = {
        labels,
        datasets: [
            {
                label: ' ()',
                data: dataSet.map(item => secondsToHours(item.timeOfWork)),
                backgroundColor: 'rgba(0, 255, 0, 1)',
            },
            {
                label: ' ()',
                data: dataSet.map(item => secondsToHours(item.timeOfPause)),
                backgroundColor: 'rgba(255, 0, 0, 1)',
            }
        ],
    };

    return (
        <div className={styles.wrapper}>
            <Bar options={options} data={data} height={500} />
        </div>
    );
}
