import React, {ReactNode, useCallback} from 'react';
import styles from './button.module.css'
import classNames from "classnames";

export interface IButtonProps{
    children:ReactNode;
    title?:string;
    type?:'circle' | 'square';
    onClick?:()=>void;
}

export function Button({ title, children, type = 'square', onClick }:IButtonProps) {
    const clickHandle = useCallback((e:React.MouseEvent<HTMLButtonElement>) => {
        e.stopPropagation();

        if (onClick) {
            onClick();
        }
    },[onClick]);

    return (
        <button className={classNames(styles.button, {[styles.circle]:type === 'circle', [styles.square]:type === 'square'})} title={title} onClick={clickHandle}>
            {children}
        </button>
    );
}
