import React from 'react';
import styles from './completedtask.module.css'
import {Button} from "../../Button";
import {EIcons, Icon} from "../../../Icons";

interface ICompletedTaskProps{
    title:string;
    onDeleteTaskClick:()=>void;
}

export function CompletedTask({title, onDeleteTaskClick}:ICompletedTaskProps) {
    return (
        <>
            <span className={styles.taskLabel}>{title}</span>

            <Button
                type='square'
                title=' '
                onClick={onDeleteTaskClick}>
                <Icon iconName={EIcons.Trash}/>
            </Button>
        </>
    );
}
