import React, {forwardRef} from 'react';
import ReactDatePicker, {registerLocale} from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css"
import ru from "date-fns/locale/ru";
import {isMonday} from 'date-fns'
import {formatDate} from "../../utils/date-api";

registerLocale("ru", ru);

export interface ICustomDatePickerProps{
    startDate:Date;
    endDate:Date;
    onChange:(date:Date | null)=>void;
}

export function CustomDatePicker({startDate, endDate, onChange}:ICustomDatePickerProps) {
    const CustomInput = forwardRef<HTMLButtonElement, {value:string, onClick:()=>void}>(({value, onClick}, ref) => (
        <button ref={ref} onClick={onClick}>
            {formatDate(startDate) + ' - ' + formatDate(endDate)}
        </button>
    ));

    return (
        <ReactDatePicker
            calendarStartDay={1}
            selected={startDate}
            onChange={(date) =>onChange(date)}
            popperPlacement='bottom-start'
            showWeekNumbers
            locale='ru'
            customInput={<CustomInput value='' onClick={() => {}}/>}
            filterDate={isMonday}
            showPopperArrow={false}/>
    );
}
