import React from 'react';
import {CustomDatePicker} from "./CustomDatePicker";
import {useDispatch, useSelector} from "react-redux";
import {RootState} from "../store/reducers";
import {setStartDate} from "../store/action-creators/statistics";
import {Layout} from "../Layout";
import {TodayStatistics} from "./TodayStatistics";
import {Block} from "../Block";
import {BarChart} from "./BarChart";
import {selectEndDate, selectStartDate} from "../store/selectors/statistics";

export function Statistics() {
    const startDate = useSelector<RootState, Date>(selectStartDate);
    const endDate = useSelector<RootState, Date>(selectEndDate);
    const dispatch = useDispatch();

    return (
        <Layout>
            <Block title='   '>
                <TodayStatistics/>
            </Block>
            <Block title='  '>
                <CustomDatePicker
                    startDate={startDate}
                    endDate={endDate}
                    onChange={(date)=>dispatch(setStartDate(date))}/>
                <BarChart/>
            </Block>
        </Layout>
  );
}
