import {ActionCreator} from "redux";
import {
    SET_START_DATE,
    SetStartDateAction, WRITE_POMIDOR_COMPLETE_EVENT, WritePomidorCompleteEventAction
} from "../actions/statistics";

export const setStartDate:ActionCreator<SetStartDateAction> = (date:Date) => {
    return {type:SET_START_DATE, date:date};
}

export const writePomidorCompleteEvent:ActionCreator<WritePomidorCompleteEventAction> = (workTime:number, pauseTime:number, numberOfStops:number) => ({
    type:WRITE_POMIDOR_COMPLETE_EVENT,
    workTime,
    pauseTime,
    numberOfStops
})