import React, {ReactNode} from "react";
import {EIcons} from "./types";
import {MinusIcon} from "./units/MinusIcon";
import {TrashIcon} from "./units/TrashIcon";

interface IIconProps{
    iconName:EIcons;
}

function getIconContent(iconName:EIcons):ReactNode{
    switch (iconName){
        case EIcons.Minus:
            return <MinusIcon/>;
        case EIcons.Trash:
            return <TrashIcon/>;
    }
}

export function Icon({ iconName }:IIconProps){
    return (
        <svg width='24' height='24'>
            {getIconContent(iconName)}
        </svg>
    )
}