import React from 'react';
import styles from './task.module.css'
import {Button} from "../../Button";
import {EIcons, Icon} from "../../../Icons";

export interface ITaskProps{
    title:string;
    isSelected:boolean;
    pomidors:number,
    onIncPomidorsClick:()=>void;
    onDecPomidorsClick:()=>void;
    onDeleteTaskClick:()=>void;
    onBlur:()=>void;
    onChangeTitle:(value:string)=>void;
}

export function Task({title, isSelected, pomidors, onIncPomidorsClick, onDecPomidorsClick, onDeleteTaskClick, onBlur, onChangeTitle}:ITaskProps) {
    const handleKeyPress = (event: React.KeyboardEvent<HTMLInputElement>): void => {
        if (event.key === 'Enter') {
            event.currentTarget.blur();
        }
    }

    return (
        <>
            {isSelected &&
                <input
                    value={title} className={styles.taskInput} autoFocus
                    onBlur={onBlur} onFocus={e => e.target.select()}
                    onChange={event => onChangeTitle(event.target.value)}
                    onKeyPress={handleKeyPress}/>}
            {!isSelected && <span className={styles.taskLabel}>{title}</span>}
            <Button type='circle' title=' ' onClick={onIncPomidorsClick}>{pomidors}</Button>
            <Button type='square' title=' ' onClick={onDecPomidorsClick}><Icon iconName={EIcons.Minus}/></Button>
            <Button type='square' title=' ' onClick={onDeleteTaskClick}><Icon iconName={EIcons.Trash}/></Button>
        </>
    );
}
