import React from 'react';
import styles from './timer.module.css'
import classNames from "classnames";
import {TimerType} from "../../store/types";

interface ITimerProps{
    minutes:number;
    seconds:number;
    type:TimerType
    onStart?:()=>void;
    onStop?:()=>void;
    onResume?:()=>void;
    onSkip?:()=>void;
    onPause?:()=>void;
    onDone?:()=>void;
    onProlongTime?:()=>void;
    title:string;
}

function getStyleName(type:TimerType):'timer-longBreak' | 'timer-shortBreak' | 'timer-pomidor'{
    switch (type){
        case TimerType.LongBreak:
            return 'timer-longBreak';
        case TimerType.ShortBreak:
            return 'timer-shortBreak';
        case TimerType.Pomidor:
            return 'timer-pomidor';
    }
}

export function Timer({minutes, seconds, type, onStart, onStop, onResume, onSkip, onPause, onDone, onProlongTime, title}:ITimerProps) {
    return (
        <div className={classNames(styles["timer"], styles[getStyleName(type)])}>
            <span className={styles.title}>{title}</span>
            <span className={styles.time}>{`${minutes} : ${seconds.toLocaleString(undefined, {minimumIntegerDigits:2})}`}</span>
            <div>
                {onStart && <button onClick={onStart} className={styles.button}></button>}
                {onPause && <button onClick={onPause} className={styles.button}></button>}
                {onStop && <button onClick={onStop} className={styles.button}></button>}
                {onResume && <button onClick={onResume} className={styles.button}></button>}
                {onSkip && <button onClick={onSkip} className={styles.button}></button>}
                {onDone && <button onClick={onDone} className={styles.button}></button>}
                {onProlongTime && <button onClick={onProlongTime} className={styles.button}> </button>}
            </div>
        </div>
  );
}
